.container {
  height: 100%;
  display: flex;
  justify-content: space-around;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0px;
  padding-top: 70px;
  width: 85%;
}